<template lang="html">
  <div>
    <div id="charts" ref="mycharts"></div>
    <div id="piecharts" ref="piecharts"></div>
  </div>
</template>

<script>
// ()
let echarts = require('echarts/lib/echarts')
// 
require('echarts/lib/chart/bar')
require('echarts/lib/chart/pie')
// title
require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')
require("echarts/lib/component/legend")


export default {
  data(){
    return{

    }
  },
  mounted(){
    this.$nextTick(
      this.drawbar());
  },
  methods:{
    drawbar(){
      // domecharts
      let chartsOne=echarts.init(this.$refs.mycharts);//refdom
      let piecharts=echarts.init(this.$refs.piecharts);

       // 
      //

      let params={};
      this.$axios.get('static/json/charts.json',params).then(res=>{
        console.log(res);
        let results=res.data;
        //
        let detailData=results.data;
        //
        let proportion=results.proportion;
        let name=proportion.map(item=>item.name);
        let value=proportion.map(item=>item.value);

        chartsOne.setOption({
          title: {
             text: results.title,
             left: 'center',
             textStyle: {
                color: '#ccc'
             }
          },
          tooltip: {},
          grid:{
          x:40,//y
          },
          xAxis: {
            //map(callback)
            data:detailData.map(item=>{return item.month}),
            axisLabel :{//0x
                 interval:0
             },
          },
          yAxis: {},
          series: [{
            name: results.about,
            type: 'bar',
            data:detailData.map(item=>{return item.total}),
          }]
        });
        piecharts.setOption({
          // backgroundColor: 'white',
         title: {
             text: results.titletwo,
             left: 'center',
             // top: 20,
             textStyle: {
                 color: '#ccc'
             }
         },
         tooltip : {
             trigger: 'item',
             formatter: "{a} <br/>{b} : {d}%"
         },

         series : [
            {
                name:results.titletwo,
                type:'pie',
                 clockwise:'true',
                 startAngle:'0',
                 radius : '60%',
                 center: ['50%', '50%'],
                 data:[
                     {
                         value:value[0],
                         name:name[0],
                         itemStyle:{
                            normal:{
                                color:'rgb(255,192,0)',
                                shadowBlur:'90',
                                 shadowColor:'rgba(0,0,0,0.8)',
                                shadowOffsetY:'30'
                             }
                         }
                     },
                     {
                        value:value[1],
                         name:name[1],
                         itemStyle:{
                            normal:{
                                 color:'rgb(1,175,80)'
                            }
                         }
                    },
                     {
                         value:value[2],
                         name:name[2],
                         itemStyle:{
                             normal:{
                                color:'rgb(122,48,158)'
                            }
                        }
                     },
                     {
                         value:value[3],
                         name:name[3],
                         itemStyle:{
                             normal:{
                                color:'rgb(64, 95, 255)'
                            }
                        }
                     }

                 ],
            }
         ]
     }
        );
      }).catch((e)=>{
        let res=require('../assets/json/charts.json');
        console.log("chartsjson"+res);

        let results=res;
        //
        //axiosdatajsonres.data
        let detailData=results.data;
        //
        let proportion=results.proportion;
        let name=proportion.map(item=>item.name);
        let value=proportion.map(item=>item.value);

        chartsOne.setOption({
          title: {
             text: results.title,
             left: 'center',
             textStyle: {
                color: '#ccc'
             }
          },
          tooltip: {},
          grid:{
          x:40,//y
          },
          xAxis: {
            //map(callback)
            data:detailData.map(item=>{return item.month}),
            axisLabel :{//0x
                 interval:0
             },
          },
          yAxis: {},
          series: [{
            name: results.about,
            type: 'bar',
            data:detailData.map(item=>{return item.total}),
          }]
        });
        piecharts.setOption({
          // backgroundColor: 'white',
         title: {
             text: results.titletwo,
             left: 'center',
             // top: 20,
             textStyle: {
                 color: '#ccc'
             }
         },
         tooltip : {
             trigger: 'item',
             formatter: "{a} <br/>{b} : {d}%"
         },

         series : [
            {
                name:results.titletwo,
                type:'pie',
                 clockwise:'true',
                 startAngle:'0',
                 radius : '60%',
                 center: ['50%', '50%'],
                 data:[
                     {
                         value:value[0],
                         name:name[0],
                         itemStyle:{
                            normal:{
                                color:'rgb(255,192,0)',
                                shadowBlur:'90',
                                 shadowColor:'rgba(0,0,0,0.8)',
                                shadowOffsetY:'30'
                             }
                         }
                     },
                     {
                        value:value[1],
                         name:name[1],
                         itemStyle:{
                            normal:{
                                 color:'rgb(1,175,80)'
                            }
                         }
                    },
                     {
                         value:value[2],
                         name:name[2],
                         itemStyle:{
                             normal:{
                                color:'rgb(122,48,158)'
                            }
                        }
                     },
                     {
                         value:value[3],
                         name:name[3],
                         itemStyle:{
                             normal:{
                                color:'rgb(64, 95, 255)'
                            }
                        }
                     }

                 ],
            }
         ]
     }
        );



      });
    },
  }
}
</script>

<style lang="less" scoped>
#charts{
  width: 700px;
  height: 600px;
  margin:10px auto;
  display: flex;
  justify-content: center;

}
#piecharts{
  width: 700px;
  height: 600px;
  margin:10px auto;
  display: flex;
  justify-content: center;
}
</style>



